import { experiences } from '@/data/experiences';
import ExperienceDetail from '@/components/ExperienceDetail';
import { notFound } from 'next/navigation';

export const dynamicParams = false; // Optional but recommended for export mode

// ✅ This is REQUIRED for static export!
export function generateStaticParams() {
  return experiences.map((exp) => ({
    slug: exp.slug,
  }));
}

// ✅ This is the dynamic page component
export default function ExperienceDetailPage({ params }: { params: { slug: string } }) {
  const experience = experiences.find((exp) => exp.slug === params.slug);

  if (!experience) {
    notFound();
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-white to-[hsl(var(--sidebar-primary-foreground))] flex flex-col">
      <ExperienceDetail experience={experience} onBack={() => history.back()} />
    </div>
  );
}
